.beams-section {
    background: url('../img/noise.png') fixed;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
}
.beams-section:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background: #010101;
    /* opacity: 0.8; */
}


.beams-section .beams {
    z-index: 2;
}
.beams-section .tables {
    width: 100%;
    margin: calc(100vh - 356px) 0 0 0;
    transform: translateX(120%);
    z-index: 2;
}
.beams-section .tables-wrapper {
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    position: relative;
}
.beams-section .tables-wrapper .table-con {
    position: relative;
    flex: 0 0 calc(100% / 3);
    display: flex;
    justify-content: center;
}
.beams-section .tables-wrapper .second-con {
    flex: 0 0 calc((100% / 3) - 40px);
}
.beams-section .tables-wrapper .third-con {
    right: calc((-100% / 3) / 2);
}
.beams-section .tables-wrapper .first-con {
    right: calc((100% / 3) / 2);
}
.beams-section .second-con {
    margin: 0 20px;
    max-width: 100%;
}
.beams-section .table-1 {
    transform-origin: 50% 100%;
}
.beams-section .parallax-swimmer {
    z-index: 2;
}
.beams-section .drops {
    z-index: 1;
}